import React, { Component }             from 'react'
import { connect }                      from 'react-redux'
import styled, {css}                    from 'styled-components'

import { userSelectors }                from 'store/ducks/user'
import { createStructuredSelector }     from 'reselect'
import { commentCreators, selectors }   from 'store/comments/comments'
import CommentForm                      from 'components/commentForm/commentForm'

// selectCurrentUser
class AddComment extends Component {
    constructor() {
        super();
        this.formInput = React.createRef();

        this.state = {
            disabled: true, // hide the comment form on load
        }
    }


    handleEnable = (e) => {
        const formState = this.state.disabled;
        this.setState({disabled: !formState}, () => {
            if (formState === true) {
                this.formInput.current.focus();
            }
        });
    }


    handleReset = (e, posted) => {
        this.setState({disabled: true});
    }


    canScroll = () => {
        return this.props.newPostRef !== null && typeof this.props.newPostRef.current !== 'undefined' && this.props.newPostRef.current !== null;
    }

    // shouldAutoScroll = () => {
    //     return !(this.props.posted.success === true && this.props.posted.type === 'root');
    // }

    // scrollToComment = () => {
    //     if (this.canScroll()) {
    //         this.props.newPostRef.current.scrollIntoView({
    //             behavior: 'smooth',
    //             block: 'center',
    //         });
    //     }
    // }


    render() {


        // if (this.canScroll() && this.shouldAutoScroll()) {
        //     this.scrollToComment();
        // }

        return (
                <SubmitContainer>
                    {(this.props.user.commenter || this.props.user.admin) &&
                        <Buttons>
                            <Button data-test="enablecomment" onClick={this.handleEnable} disabled={!this.state.disabled}>Post a comment</Button>
                        </Buttons>
                    }

                    {!this.props.user.commenter && !this.props.user.admin &&
                        <Buttons>
                            <Apply href="" target="_blank" data-test="applycomment">Become a commenter</Apply>
                        </Buttons>
                    }

                    {this.state.disabled && this.props.posted.success === true && this.props.posted.type === 'root' && 
                        <>
                            <PostMessageContainer>
                                <Thanks>Thanks {this.props.user.details.firstname}, your comment is live.</Thanks>
                                <Faq>Questions? Check out our comments FAQ</Faq>
                            </PostMessageContainer>
                            <Scroll onClick={this.props.scrollHandler}>VIEW MY COMMENT</Scroll>
                        </>
                    }

                    {!this.state.disabled && this.props.user.loggedIn &&
                        <Welcome>Hi {this.props.user.details.firstname}, thanks for joining the conversation</Welcome>
                    }

                    <div>
                        {!this.state.disabled && 
                            <CommentForm    
                                ref={this.formInput} 
                                reply_to={false}
                                path={null}
                                submitHandler={this.props.postComment}
                                resetHandler={this.handleReset}
                            />
                        }

                    </div>

                </SubmitContainer>

        )
    }
}

const SubmitContainer = styled.div` 
    margin-bottom:69px;
`


const Buttons = styled.div`
    display:flex;
    /* justify-content:flex-end; */
    /* margin-left:auto; */
`
const PostMessageContainer = styled.div`
    display:flex;
    justify-content:space-between;
    margin-top:40px;
    margin-bottom:6px;
`

const Thanks = styled.p`
    font-family: "Source sans pro";
    font-size:15px;
    font-weight:600;
`

const Scroll = styled.button`
    color: #e90202;
    font-family: "Source sans pro";
    font-size:15px;
    font-weight:600;
    text-transform:uppercase;
    border:none;
    background:none;
    padding:0;
    cursor: pointer;
    &:focus {
        outline:none;
    }
`

const Faq = styled.p`
    font-family: "Source sans pro";
    font-size:15px;
`


const Button = styled.div`
    font-family: "Source sans pro";
    justify-content:center;
    display:flex;
    flex-basis:100%;
    font-size:14px;
    font-weight:700;
    text-align:center;
    letter-spacing: 0.6px;
    padding:9px;
    text-transform:uppercase;
    cursor:pointer;
    border: 2px solid #e22f22;
    color: black;
    ${props => props.disabled && css`
        border-color: #c6c6c7;
        color: #c6c6c7;
        cursor:unset;
        margin-bottom:31px;

    `}
`

const Apply = styled.a`
    font-family: "Source sans pro";
    justify-content:center;
    display:flex;
    flex-basis:100%;
    font-size:14px;
    font-weight:700;
    text-align:center;
    letter-spacing: 0.6px;
    padding:9px;
    text-transform:uppercase;
    cursor:pointer;
    border: 2px dotted #e90202;
    color: black;
`


const Welcome = styled.p`
    font-family: "Source sans pro";
    font-size:15px;
    font-weight:600;
    margin-bottom:20px;
`


const mapStateToProps = createStructuredSelector({
    user: userSelectors.selectUser,
    posted: selectors.selectPosted,
    newPostRef: selectors.selectNewPostRef,
});

const mapDispatchToProps = dispatch => {
    return {
        postComment: (comment) => dispatch( commentCreators.postComment(comment) )
    }
};

export default connect(mapStateToProps, mapDispatchToProps)(AddComment);