import React, {Component} from 'react';
import CommentsContainer from 'containers/comments/comments'
import CommentForm from 'containers/comments/addComment'
import Error from 'containers/error/error'


class App extends Component {

    constructor(props) {
        super(props);
        this.scrollTo = {};
    }

    scroll = () => {
        if (this.scrollTo && this.scrollTo.current) {
            this.scrollTo.current.scrollIntoView({
                behavior: 'smooth',
                block: 'center',
            });
            this.scrollTo = {};
        }
    }


    addRef = (ref) => {
        this.scrollTo = ref;
    }

    render()  {


        return (
            <div className="App">
                <Error />
                <CommentForm refHandler={this.addRef} scrollHandler={this.scroll} />
                <CommentsContainer refHandler={this.addRef} />
            </div>
        );

    }
}

export default App;
