import React, { Component }         from 'react'
import {connect}                    from 'react-redux'
// import {withRouter}                 from 'react-router'
// import qs                           from 'qs'
// import axios                        from 'axios'
import {createStructuredSelector}   from 'reselect'
import styled, {css}                from 'styled-components'
import Select                       from 'react-select'

// STORE
import { userSelectors } from 'store/ducks/user'
import { selectors, commentCreators, commentTypes }   from 'store/comments/comments'
import { appSelectors }             from 'store/app/app'

// COMPONENTS
import CommentThread                from 'components/comments/commentThread'
import RippleSpinner                from 'components/spinners/RippleSpinner'
import Threaded                     from 'components/icons/thread/threaded'
import Refresh                      from 'components/icons/refresh'
import size                         from 'components/layout/responsive'


class CommentsContainer extends Component {

    constructor(props, context) {
        super(props, context);
    
        this.scrollTo = {};
    }
    


    componentDidMount() {

         // If url hash is set, we'll load that single comment
        // and it's parents (usually for moderation purposes)
        var hash = window.location.hash.substring(3); //Puts hash in variable, and removes the # character
        if (Number.isInteger(parseInt(hash))) {
            // tell redux to expect this comment id so we can scroll to it after fetch
            this.props.select(hash);
            // this.props.urlLoad(true);
            this.props.fetchComments(hash, [], {'parents': true});
        } 
    }

    componentDidUpdate(prevProps, prevState) {

        if (this.scrollTo !== false) {
            this.scroll();
        }
    }


    addRef = (ref) => {
        this.scrollTo = ref;
        this.props.refHandler(ref);
    }


    scroll = () => {
        if (this.scrollTo && this.scrollTo.current && !this.props.posted.id) {
            this.scrollTo.current.scrollIntoView({
                behavior: 'smooth',
                block: 'center',
            });
            this.scrollTo = {};
        } 
    }


    refresh = () => {
        const params = {};
        if (!this.props.user.admin) {
            params['toppicks'] = true;
        }
        this.props.urlLoad(false);
        this.props.fetchComments(null, [], params);
    }


    render() {


        const customStyles = {
            option: (provided, state) => {
                return {...provided};
            },

            container: (provided, state) => {
                return {
                    ...provided,
                    width: "200px"
                }
            },
            control: (provided, state) => {
                const styles = {
                    ...provided,
                    borderRadius: 0,
                    borderColor: "#e7e7e7",
                    height:'30px',
                    minHeight:'10px',
                }
                if (state.isDisabled) {
                    styles.backgroundColor = 'white';
                    styles.color = 'black';
                }
                return styles;
            },
            valueContainer: (provided, state) => {
                const styles = {
                    ...provided,
                    padding: '0 8px',
                }
                return styles;
            },
            dropdownIndicator: (provided, state) => {
                const styles = {
                    ...provided,
                    padding: '2px',
                }
                if (state.isDisabled) {
                    styles.color = 'white';
                }
                return styles;
            },
            indicatorSeparator: (provided, state) => {
                const styles = {
                    ...provided
                }
                if (state.isDisabled) {
                    styles.backgroundColor = 'white';
                }
                return styles;
            },
            indicatorsContainer: (provided, state) => {
                const styles = {
                    ...provided
                }
                if (state.isDisabled) {
                    styles.display ="none";
                }
                return styles;
            },
        };

        const StatusOptions = [
            { value: '', label: 'All'},
            { value: 'published', label: 'Published'},
            { value: 'draft', label: 'Draft'},
            { value: 'disapproved', label: 'Disapproved'},
            { value: 'deleted', label: 'Deleted'},
        ];
        const selectedIndex = ['', 'published', 'draft', 'disapproved', 'deleted'].indexOf(this.props.status);
        const { count }  = this.props;
        const { comments } = this.props;

        return (
            <div>
                <Header>
                    <Title>{count} comments</Title>
                    <Threadcount onChange={(e) => this.props.updateThreadLevel(e.target.value)} value={this.props.threadLevel} disabled={!this.props.threaded}/>
                    <Threaded active={this.props.threaded === true} handler={this.props.toggleThreaded} />
                    
                    
                    {this.props.user.admin &&
                        <SelectContainer>
                            <Select   
                                styles      = {customStyles} 
                                // isDisabled  = {props.active} 
                                onChange    = {this.props.updateStatusView}
                                value       = {StatusOptions[selectedIndex]}
                                options     = {StatusOptions}
                            />
                        </SelectContainer>
                    }
                    <RefeshContainer>
                        <LimitContainer>
                            <LimitLabel>Limit</LimitLabel>
                            <Limit onChange={(e) => this.props.updateLimit(e.target.value)} value={this.props.limit} />
                        </LimitContainer>
                        <Refresh active={this.props.refresh}  handler={this.refresh} />
                    </RefeshContainer>
                </Header>
                <CommentThread comments={comments} refHandler={this.addRef} selected={this.props.selected} scrollHandler={this.scroll} />

                {this.props.refresh &&
                    <LoadPending>
                        <RippleSpinner />
                    </LoadPending>
                }



                {this.props.showLoadMore && 
                    <LoadMore onClick={this.props.loadMore}>Load more</LoadMore>
                }

            </div>
        )
    }
}

const LoadPending = styled.div`
    display:flex;
    align-items:center;
    justify-content:center;
    height: 30px;
    margin-bottom: 70px;
`

const Header = styled.div`
    display:flex;
    border-bottom: 4px solid black;
    margin-bottom:25px;
    align-items:center;
    padding-bottom: 17px;
` 

const RefeshContainer = styled.div`
    margin-left:auto;
    display:flex;
    align-items:center;
`
const SelectContainer = styled.div`
    margin-left:40px;
    display:flex;
`



const Threadcount = styled.input`
    width: 30px;
    height: 25px;
    text-align: center;
    border: none;
    margin-left: 10px;
    color: lightgoldenrodyellow;
    background: rgba(0,0,0,0.8);
    ${props => props.disabled && css`
        opacity:.5;
    `}
`

const LimitContainer = styled.div`
    display:flex;
    flex-direction:column;
    margin-right:7px;
`
const LimitLabel = styled.label`
    font-family: "Source Sans Pro";
    font-size:12px;

`

const Limit = styled.input`
    width: 26px;
    height: 17px;
    text-align: center;
    border: none;
    /* margin-left: 10px; */
    color: lightgoldenrodyellow;
    background: rgba(0,0,0,0.8);
`

const Title = styled.h3` 
    font-family: "Source Sans Pro";
    font-size:22px;
    font-weight:600;

    ${_ => size.mobile(`
        font-size:18px;
    `)}


`

const LoadMore = styled.p`
    font-family: "Source Sans Pro";
    display: inline-block;
    font-size: 17px;
    color: rgb(226, 47, 34);
    padding: 10px;
    border: 1px solid rgb(226, 47, 34);
    font-weight:600;
    cursor:pointer;

`

const mapStateToProps = createStructuredSelector({
    user: userSelectors.selectUser,
    count: selectors.selectCommentCount,
    limit: selectors.selectLimit,
    posted: selectors.selectPosted,
    status: selectors.selectStatusView,
    refresh: selectors.selectRefresh,
    comments: selectors.selectAllComments,
    selected: selectors.selectSelectedComment,
    threaded: selectors.selectThreaded,
    articleId: appSelectors.articleSelector,
    rootCount: selectors.selectRootCount,
    // newPostRef: selectors.selectNewPostRef,
    loadedCount: selectors.selectLoadedRootCount,
    threadLevel: selectors.selectThreadLevel,
    showLoadMore: selectors.loadMore,

});

const mapDispatchToProps = dispatch => {
    return {
        select: (id) => dispatch( {type: commentTypes.SELECT, payload: id} ),
        urlLoad: (status) => dispatch( {type: commentTypes.URL_LOAD, payload: status} ),
        loadMore: () => dispatch( commentCreators.fetchComments(null, [], {'more': true}) ),
        updateLimit: (limit) => dispatch( {type: commentTypes.LIMIT, payload: limit}) ,
        fetchComments: (id, path, params) => dispatch( commentCreators.fetchComments(id, path, params) ),
        toggleThreaded: () => dispatch( {type: commentTypes.THREADED, payload: null}) ,
        updateStatusView: (status) => dispatch( {type: commentTypes.UPDATE_STATUS_VIEW, payload: status.value} ),
        updateThreadLevel: (level) => dispatch( {type: commentTypes.THREAD_LEVEL, payload: level} ),
    }
};

export default connect(mapStateToProps, mapDispatchToProps)( CommentsContainer );